@import "../../helpers/utils";

.section {
  &__title {
    font-size: 32px;
    margin-bottom: 24px;

    @extend %mont-bold-style;
  }
}

.category {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-direction: row;

  @include on-tablet {
    flex-direction: column;
  }

  &__image {
    position: relative;
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: left;
    transition: scale $transition-duration;

    top: 10%;
    left: 20%;

    &--tablets {
      top: 15%;
      transform: scale(1.2);
    }

    &--accessories {
      left: 10%;
    }
  }

  &__photo {
    height: 368px;
    width: 368px;
    margin-bottom: 24px;

    overflow: hidden;

    @include on-mobile {
      height: 288px;
      width: 288px;
    }

    &:hover > .category__image {
      scale: 1.1;
    }

    &--phones {
      background-color: bisque;
    }

    &--tablets {
      background: #8b8b90;
    }

    &--accessories {
      background: #973d5f;
    }
  }

  &__title {
    font-size: 20px;

    @extend %mont-semibold-style;
  }

  &__text {
    @extend %body-text;

    color: $gray-secondary;
  }
}
